body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

p {
  margin: 0.5rem;
}

h4 {
  margin: 0.5rem;
}

h2 {
  margin: 0.5rem;
}

label {
  margin: 0.5rem;
}

.btn-classic {
  background-color: gray;
  color: white;
}

.btn-classic:hover {
  background-color: rgb(102, 102, 102);
  color: white;
}

.btn-delete {
  background-color: lightcoral;
  color: black;
}

.btn-delete:hover {
  background-color: rgb(235, 89, 89);
  color: black;
}

.btn-edit {
  background-color: orange;
  color: black;
}

.btn-edit:hover {
  background-color: rgb(202, 131, 0);
  color: black;
}

.btn-edit svg {
  width: auto;
  height: auto;
  margin-top: auto;
}

.btn:focus {
  box-shadow: none;
}

.footer,
.header {
  background-color: rgb(233, 255, 255);
}

.band {
  background-color: rgba(62, 154, 131, 0.599);
}

body {
  padding: 86px 0px;
  margin-left: auto;
  margin-right: auto;
}
.header {
  left: 0;
  top: 0;
  position: fixed;
  border-bottom-style: solid;
  border-color: #d6d6d6;
  border-width: 1px;
  padding: 7px 20px;
  width: 100%;
}
.header-left {
  padding: 9px 0;
  vertical-align: top;
  display: inline-block;
  text-align: left;
}
.header-right {
  padding: 21px;
  margin: auto;
  float: right;
}

.menu-button {
  font-size: 17px;
  color: rgba(0, 0, 0, 0.7);
  font-weight: bold;
  background-color: transparent;
  border-color: transparent;
  border-radius: 0.25rem;
}

.menu-button:hover {
  color: black;
  font-weight: bold;
  background-color: transparent;
  border-color: transparent;
}

.logo {
  float: left;
  display: inline-block;
  padding: 15px 5px;
}

.footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  color: black;
  text-align: center;
}
.footer-left {
  display: inline-block;
  text-align: left;
  width: 80%;
}
.footer-right {
  display: inline-block;
  text-align: right;
}

.home {
  background-image: url(/static/media/background.acfe9146.jpg);
  height: auto;
  background-size: 100%;
}

.text {
  color: #fff;
  font-size: 50px;
  text-align: center;
  padding: 60px 0px;
}

.sep-overview,
.sep-admin {
  padding-bottom: 25px;
}

.grid-tile {
  min-width: 150px;
  display: grid;
  padding: 15px 5px;
  align-content: center;
  border-radius: 7px;
  background-color: rgb(243, 243, 243);
}

.svg-container {
  display: flex;
  justify-content: center;
}

.headline {
  text-align: center;
  margin: 20px;
  font-size: 1.8rem;
}

.grid {
  display: grid;
  align-content: center;
  justify-content: center;
  grid-gap: 50px;
  gap: 50px;
  grid-auto-flow: column;
}

.container {
  display: flex;
}
.body {
  padding: 20px;
  margin-bottom: -32px;
}

.form {
  text-align: center;
  border: 1px solid;
  margin: 2% 35%;
  padding: 50px;
  border-radius: 12px;
  padding-top: -30px;
}

.field {
  text-align: left;
  margin: 15px 0px;
}

.oneLineText {
  width: 100%;
  border-top: none;
  border-left: none;
  border-right: none;
}

svg {
  width: 110px;
  height: 100px;
  margin-top: -4px;
  margin-bottom: 5px;
}

.body-text-center {
  text-align: center;
}

.textfield {
  width: 95%;
  text-align: center;
}

.table {
	text-align: center;
	border: 2px solid #d3d3d3;
	width: 95%;
	margin:auto;
	line-height: 5px;
	border-collapse: collapse;
}

.sidebar {
	text-align: center;
	width:120px;
	min-width:120px;
}

.cell, .cell-booked, .t-row, .t-head{       
	border: 2px solid #d3d3d3;
}

.t-head, .sidebar {
	background-color: rgb(233, 255, 255);
}

.cell-booked {
	background-color: rgba(189, 212, 212, 0.747);
}

.cell-booked-user {
	background-color: rgba(62, 154, 131, 0.599);
}

.cell-selected {
	background-color: orange;
}

.td {
	padding:0;
}

.saveButton {
	float: right;
	margin: 2%;
	background-color: transparent;
	border: 1px solid;
	border-radius: 4px;
	padding: 5px 10px;
	font-weight: bold;
}

.div-hidden {
	display: none;
}

.star {
	color:rgba(189, 212, 212, 0.747);
}

.star:hover, .star-clicked {
	color: rgb(255, 189, 91);
}

.textfield {
	margin: 3%;
}

.ratingButton {
	float: right;
	margin-right: 5%;
	background-color: transparent;
	border: 1px solid;
	border-radius: 4px;
	padding: 5px 10px;
	font-weight: bold;
}


